*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: 'Courier New', Courier, monospace;
  user-select: none;
}
.App{
    min-height: 100vh;
    background-color: aliceblue;
}
.container-form{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  height: 300px;
}
.form{
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  
  height: 80%;
}
.container-input{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
  height: 100%;
}
.btn-agregar{
  width: 30%;
  margin-bottom: 20px;
  background-color: white;
  font-size: 15px;
  height: 30px;
  border-radius: 20px;
}
.container-table{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 10px;
  min-height: 40vh;
}
.btn-agregar:hover{
  border: 2px solid rgb(139, 137, 137);
}
.btn-eliminar{
  transition-duration: 0.4s;
  background-color: #f44336;
  color: white;
  padding: 4px 7px;
  border: 2px solid  #f44336;
}
.btn-eliminar:hover {
  background-color:white ;
  color: black;
}
input{
  width: 50%;
  height: 27px;
  font-size: 20px;
}
input:hover{
  border: 2px solid rgb(121, 121, 249);
}
h1{
  font-size: 44px;
}
table, th, td {
  border: 1px solid;
  background-color:white;
}
table{
  border-collapse: collapse;
  width: 70%;
  text-align: center;
  font-size: 18px;
}
th{
  height: 50px;
}
td{
  height: 35px;
}


